@import "../../../styles/themes.scss";
.header-warp{
  width: 100%;
  min-width: 1200px;
  height: 50px;
  background-color:rgba(18,24,38,1);
  .gangg{
    border-left: 1px solid #162D59;
    height: 18px;
    margin-top: 16px;
  }
  .biaotiaicm{
    position: absolute;
    top: -15px;
    right: -14px;
    color: #F9692E;
  }
 
.navlinkuo{
  color: #898E98 !important;
  border-bottom:3px solid transparent;
  position: relative;
  img{
    width: 16px;
    height: 10px;
    position: absolute;
    top: 9px;right: 5px;
  }
}

  .loginHover:hover .loginHoverhezi{
    display: block;
  }
  .loginHover:hover span{
   color: #fff !important;
  }
  .longin{
    .colormy{
      color: #4D6595 !important;
    }
    .selectedaaa{
      color: #fff !important;
    }
    .navlinkuo{
      margin-right: 16px;
    }
    .navlinkuo:hover{
      color:#fff !important;
    }
    // width: 100px !important;
    //float: left;
    display: flex;
    align-items: center;
  
    .img5{
      width: 35px;
      height: 50px;
      margin-left: 8px;
      span{
        display: inline-block;
        line-height: 50px;
        color: #4D6595;
      }
    }
    .img5:hover{
      width: 35px;
      height: 50px;
      margin-left: 8px;
      span{
        color: #fff;
      }
    }
    .img6{
      width: 35px;
      height: 50px;
      position: relative;
      span{
        display: inline-block;
        line-height: 50px;
        color: #4D6595;
      }
    }
    .img6:hover{
      width: 35px;
      height: 50px;
      span{
        color: #fff;
      }
    }
    .loginHover{
      cursor: pointer;
     .halflsk{
       position: relative;
        line-height: 50px;
        text-align: right;
        margin-right: 12px;
        cursor:pointer;
        span{
          display: block;
          float: left;
          color:#888E98;
        }
        span:nth-child(2){
          line-height: 45px;
        }
