  }

  .header-left{
    display: inline-block;
    height: 100%;
    overflow: hidden;
    >img{
      width: 89px;
      height: 26px;
      margin-top: 12px;
      margin-left: 20px;
      display: inline-block;
      float: left;
    }
    >img:hover{
      cursor: pointer;
    }
    >ul{
      height: 100%;
      float: left;
      >a{
        margin-left: 30px;
        text-align: center;
        font-size:14px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(230,234,242,1);
        line-height:50px;
        height: 100%;
        float: left;
      }
      a:hover{
        color:#E4ECF8 !important;
      }
    }
  }
  .header-right{
    height: 100%;
    float:right;
    margin-right: 37px;
    color: aliceblue;
    display: flex;
    .font_ic{
      height: 50px;
      padding: 0 5px;
      .imgtupian1{
        position: relative;
        width: 35px;
        float: left;
        text-align: center;
        line-height: 50px;
        padding: 0 10px;
        color: #4d6595;
        cursor: pointer;
        .notice_box{
          text-align: left;
          display: none;
          width: 300px;
          position: absolute;
          right: 0;
          top: 46px;
          @include themify($themes) {
            background-color: themed('background-color-32' ) ;
          }
          border-radius:4px;
          box-sizing: border-box;
          color:rgba(102,102,102,1);
          z-index: 999;
          overflow: hidden;
          .title_box{
            padding: 0 19px 0 15px;
            line-height: 35px;
            color:rgba(153,153,153,1);
            font-size: 12px;
          }
          .footder_box{
            padding: 0 19px 5px 15px;
            text-align: center;
            line-height: 35px;
            color:#2F6EEC;
            font-size: 12px;
          }
            .itend_information{
              padding: 0 19px 0 15px;
              line-height: 42px;
              @include themify($themes) {
                color: themed('color-17' ) ;
              }
              font-size: 14px;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
              display: block;
            }
            .itend_information:hover{
              @include themify($themes) {
                background-color: themed('background-color-33' ) ;
              }
            }
        
        }
      }
      .imgtupian1:hover{
        color: #fff;
      }
      .img6:hover .notice_box{
        display: block;
      }

      // 平台公告
      .hexizbox{
        position: absolute;
        width: 250px;
        height: 120px;
        top: 46px;
        right:0;
        z-index: 9999;
             @include themify($themes) {
                background-color: themed('background-color-32' ) ;
              }
        border-radius:4px;
        display: none;
        .androidclass{
          @include themify($themes) {
            color: themed('color-17' ) ;
          }
          font-size:14px;
          font-family:Microsoft YaHei;
         display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: center;
          line-height: 1.5;
         .div-chakangengduo{
           line-height: 30px;
          font-size:14px;
          font-family:Microsoft YaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
         }
        }
      }
      .img5:hover .hexizbox{
        display: flex;
      }
      
    }
   
  
    .imgtupian{
      width: 35px;
    }
    .loginHoverhezi{
      display: none;
      width:500px;
      height:210px;
      position: absolute;
      top: 43px;
      right: 6px;
      z-index: 11;
      .neiloginHOoverhezi{
        float: right;
        border-radius:4px;
        overflow: hidden;
        @include themify($themes) {
          background-color: themed('background-color-32' ) ;
        }
      }
      li:hover{
        @include themify($themes) {
          background-color: themed('background-color-33' ) ;
        }
      }
      a:hover{
        @include themify($themes) {
          background-color: themed('background-color-33' ) ;
        }
      }
      li{
        cursor: pointer;
        line-height: 42px;
        padding-left: 15px;
        padding-right: 15px;
        font-size:14px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        @include themify($themes) {
          color: themed('color-17' ) ;
        }
      }
      a{
        display: block;
        cursor: pointer;
        line-height: 42px;
        padding-left: 15px;
        padding-right: 15px;
        font-size:14px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        @include themify($themes) {
          color: themed('color-17' ) ;
        }
      }
    }


    .navlinkuo{
      margin-right: 16px;
      line-height: 50px;
    }
    .navlinkuo:hover{
      color:#fff !important;
    }
    .cheze{
      display: inline-block;
      color:#E4ECF7;
      margin:  11px 15px 11px 0;
      width: 60px;
      height: 28px;
      line-height: 28px;
      background:#2E6BE6;
      border-radius:4px;
      text-align: center;
    }
    .cheze:hover{
      background:#3377FF;
    }
    .cheze:active{
      background:#295FCC;
    }
    .cheze2{
      width: 50px;
      color:#E4ECF7;
      display: inline-block;
    }
    .cheze2:hover{
      color:#E4ECF7;
    }
  }

   // 资产列表
  .xuanxiang{
    position: relative;
    cursor:pointer;
    // width: 125px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .inputxiala{
        margin-left: 14px;
        margin-right: 10px;
    }
    .ggang{
      border-left: 1px solid #162D59;
      height: 18px !important;
    }
    .sximg{
      width: 20px;height: 20px;
      margin-right: 14px;
      margin-top: 14px;
      background:url('../../img/nav_down.png') no-repeat;
    }
   
    .inpusll_wawrp_box{
      display: none;
      height: 1000px;
      position: absolute;
      top: 45px;
      right: 1px;
      .inpusll-wawrp{
        display: none;
        position: absolute;
        top:0;
        right: 0;
        border-radius:4px;
        z-index: 999;
        overflow: hidden;
        @include themify($themes) {
          background-color: themed('background-color-34' ) ;
        }
        li{
          float: left;
          height: 50px;
          width: 150px;
          font-size:14px;
          font-family:Microsoft YaHei;
          font-weight:400;
          color:rgba(136,142,152,1);
          align-items: center;
          justify-content: left;
          display: flex;
          @include themify($themes) {
            background-color: themed('background-color-34' ) ;
          }
          @include themify($themes) {
            color: themed('color-17' ) ;
          }
          .bizhong{
            width: 24px;
            height: 24px;
            // border: 1px solid #b8becc;
            // margin: auto 0;
            margin-left: 16px;
            line-height: 19px;
            text-align: center;
            border-radius: 12px;
            position: relative;
            margin-right: 10px;
            .icon_img_box{
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: 20px;
              height: 20px;
            }
          }
        }
        li:hover{
          @include themify($themes) {
            background-color: themed('background-color-33' ) ;
          }
        }
      }
    }
   

  }
  .xuanxiang:hover{
    .inpusll_wawrp_box{
      display: block;
    }
    .inpusll-wawrp{
      display: block;
    }
    .sximg{
      background: no-repeat url('../../img/nav_up.png');
    }
  }
  // 多语言切换
  .guojihua{
    width: 100px;
    margin-left: 10px;
    height: 50px;
    line-height: 50px;
    position: relative;
    .guojihua_box_wrp{
      display: none;
      position: absolute;
      z-index: 9999;
      top: 46px;
      left: 0;
      border-radius: 4px;
      overflow: hidden;
      li{
        @include themify($themes) {
          background-color: themed('background-color-34' ) ;
        }
        @include themify($themes) {
          color: themed('color-17' ) ;
        }
      }
      li:hover{
        @include themify($themes) {
          background-color: themed('background-color-33' ) ;
        }
      }
     
    }
    .imgjianto{
      display: inline-block;
      width: 20px;height: 13px;
      margin-left: 10px;
      vertical-align: middle;
      background:url('../../img/nav_down.png') no-repeat;

    }
  }
  .guojihua:hover{
    .guojihua_box_wrp{
      display: block;
      width: 100px;
    }
    .imgjianto{
      background:url('../../img/nav_up.png') no-repeat;
    }
  
  }
}

.action-inputxiala{
  @include themify($themes) {
    background-color: themed('background-color-33' ) ;
  }
  color: #2F6FED !important;
}

@media screen and (max-width:1450px) {
  .header-warp{
    min-width: 1200px;
  }

}

